*{
    margin: 0;
  }
  .App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .blank{
    color: red;
  }
  
  .bold{
    font-weight: bold;
  }
  
  .accordion-button{
    border: 1px solid grey;
  }
  
  .accordion-button::after{
    filter: invert(1);
  }
  
  a {
      /* color: rgba(var(--bs-link-color-rgb),var(--bs-link-opacity,1)); */
      text-decoration: none;
  }

  
  li a:hover{
    font-size: 1.25vw;
    transition: 0.5s;
    transition-timing-function: ease-out;
    font-weight: 450;
  }
  

  .navbar{
    height: 4vw;
  }

  #alertbar{
    height: 4vw;
  }
  #RedTheme, #GreenTheme{
    height: 2vw; 
    width: 2vw;
    cursor: pointer;
    border: 1px solid white;
  }

  .flexContainer{
    display: flex;
  }

  .d-flex{
    margin: 0.5vw;
  }


